or: #ff0000;\r\n    --yelow_color: #ffba00;\r\n    --green_color: #21b624;\r\n}\r\n\r\n.bg_color {\r\n    --background: var(--bg-color);\r\n}\r\n\r\nion-item {\r\n    --ripple-color: var(--transparent);\r\n}\r\n\r\n.d-flex {\r\n    display: flex !important;\r\n    align-items: center;\r\n    width: 100%;\r\n\r\n    .end {\r\n        margin-left: auto !important;\r\n        margin-right: 0 !important;\r\n        display: block;\r\n    }\r\n}\r\n\r\n.center_img {\r\n    position: relative;\r\n    overflow: hidden;\r\n\r\n    img {\r\n        &.crop_img {\r\n            position: absolute;\r\n            min-width: 100%;\r\n            min-height: 100%;\r\n            top: 0;\r\n            bottom: 0;\r\n            left: 0;\r\n            right: 0;\r\n            margin: auto;\r\n        }\r\n    }\r\n}\r\n\r\n.button {\r\n    &.btn {\r\n        width: 100%;\r\n        font-size: 1.1rem;\r\n        font-weight: 400;\r\n        letter-spacing: 0.6px !important;\r\n        height: 50px;\r\n        --background: var(--primary);\r\n        text-transform: none;\r\n        color: var(--white);\r\n        margin: 0;\r\n        --box-shadow: none;\r\n\r\n        ion-icon {\r\n            font-size: 1.4rem;\r\n        }\r\n\r\n        &:hover:not(.disable-hover) {\r\n            --background: var(--primary);\r\n        }\r\n\r\n        &.button-outline {\r\n            --background: var(--transparent);\r\n            color: var(--white);\r\n            --border-color: var(--primary);\r\n            --border-width: 1px;\r\n\r\n            &:hover:not(.disable-hover) {\r\n                --background: var(--transparent);\r\n            }\r\n        }\r\n\r\n        &.button-round {\r\n            --border-radius: 50px !important;\r\n        }\r\n\r\n        &.button-block {\r\n            --border-radius: 6px !important;\r\n        }\r\n    }\r\n}\r\n\r\nion-header {\r\n    z-index: 999;\r\n\r\n    &::after {\r\n        display: none !important;\r\n    }\r\n\r\n    ion-toolbar {\r\n        --background: var(--white);\r\n        display: flex;\r\n        align-items: center;\r\n        min-height: 60px !important;\r\n        height: auto;\r\n        overflow: visible !important;\r\n        --border-width: 0 !important;\r\n\r\n        ion-buttons {\r\n            position: relative !important;\r\n            margin: 0;\r\n            --padding-end: 0 !important;\r\n            --padding-start: 0 !important;\r\n            --color: var(--text-black) !important;\r\n\r\n            .button-inner {\r\n                ion-icon {\r\n                    //font-size: 1.5rem;\r\n                    padding-left: 00px;\r\n                    color: var(--text-black);\r\n                }\r\n            }\r\n\r\n            ion-back-button {\r\n                --color: var(--text-black) !important;\r\n                color: var(--text-black) !important;\r\n            }\r\n\r\n            ion-menu-button {\r\n                --color: var(--text-black) !important;\r\n                //--padding-start: 0 !important;\r\n                --padding-end: 0 !important;\r\n                font-size: 1.8rem !important;\r\n\r\n                &.ios {\r\n                    font-size: 2rem !important;\r\n                }\r\n            }\r\n\r\n            ion-back-button {\r\n                .button-native {\r\n                    padding: 0 !important;\r\n                }\r\n            }\r\n\r\n            &.ios {\r\n                padding: 0 10px !important;\r\n            }\r\n        }\r\n\r\n        ion-title {\r\n            color: var(--text-black);\r\n            text-align: justify;\r\n            font-size: 1.1rem;\r\n            font-weight: 500;\r\n            overflow: visible !important;\r\n            line-height: 60px;\r\n            position: absolute !important;\r\n            left: 0;\r\n            top: -2px;\r\n            width: 100%;\r\n            text-align: center;\r\n            padding: 0 15px !important;\r\n        }\r\n    }\r\n